.main{
    display: flex;
    flex-wrap: wrap;
    font-family: cursive;
    justify-content: center;
}

.aside{
    background-color: black;
    color: white;
    padding: 10px;
    text-align: left;
    border-radius: 30px 0px 0px 30px;
    max-width: 300px;
}

img{
    width: 200px;
    height: 200px;
    border: 1px solid black;
    border-radius: 50%;
    margin-left: 50px;
    margin-top: 20px;
}

.body{
    padding-left: 20px;
    text-align: left;
    background-color: bisque;
    max-width: 1009px;
}

h3{
    color: rgb(101, 101, 180);
}